import WeatherConfiguration from './WeatherConfiguration.js'
import CurrentWeather from './CurrentWeather.js'
import HourlyWeather from './HourlyWeather.js'
import DailyForecast from './DailyForecast.js'
import DayForecastModel from './DayForecastModel.js'
import OpenWeatherApiAdapter from './OpenWeatherApiAdapter.js'

function retrieveDayNameByDate(date){
	return Intl.DateTimeFormat('en-US', { weekday: 'long'}).format(date)
}

function tabulateHourlyWeatherRecordsIntoDailyForecast(hourlyRecords){
	let currentIndex = -1
	let currentDay
	let array = []
	hourlyRecords
	.forEach((hourlyRecord) => {
		if(hourlyRecord.date.getDate() !== currentDay) {
			currentDay = hourlyRecord.date.getDate()
			currentIndex++
			array[currentIndex] = []
		}
		array[currentIndex].push(hourlyRecord)
	})
	return array
}

export default {
	assignCityAndUnitFamily(city,unitFamily){
		WeatherConfiguration.updateCity(city);
		WeatherConfiguration.updateUnitFamily(unitFamily);
		this.refresh();
	},
	refresh(){
		OpenWeatherApiAdapter.retrieveCurrentWeatherInfo(WeatherConfiguration.city,WeatherConfiguration.unitFamily)
		.then((result) => {
			CurrentWeather.updateBasicWeatherInfo(result);
		})
		OpenWeatherApiAdapter.retrieveWeatherTemperatureAndConditionForNextHours(WeatherConfiguration.city,WeatherConfiguration.unitFamily,5)
		.then((result) => {
			HourlyWeather.updateHourlyForecast(result);
		})
		OpenWeatherApiAdapter.retrieveWeatherTemperatureAndConditionForNextHours(WeatherConfiguration.city,WeatherConfiguration.unitFamily,20)
		.then((result) => {
			DayForecastModel.updateDailyWeatherForecast(
				tabulateHourlyWeatherRecordsIntoDailyForecast(result)
				.map((hourlyForecastForOneDay) => {
					return new DailyForecast(hourlyForecastForOneDay)
				})
				.map((dailyForecast) => {
					return {
						day: retrieveDayNameByDate(dailyForecast.date()),
						highTemperature: dailyForecast.highTemperature(),
						lowTemperature: dailyForecast.lowTemperature()
					}
				})
			)
		})
	}
}



